
.mainBlock {
  
  min-height: 100vh;
  display: flex;
  align-items: center;
  background-color: seashell;

  
  .mainContainer {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    
    display: flex;
    
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    border: 1px solid;

    
    .info {
      
      border-radius: 3px;
      background-color: ghostwhite;
    }

    .photo {
      width: 200px;
      height: 350px;
      
      
      border-radius: 0 30px 30px 0;
      transition: width 2s;
      opacity: 0.97;
      z-index: 30;
    }

  }
}

.mainBackImg{
  position: fixed;
  
  
  
  top: 100px;
  

  color: rgba($color: #cec7c7, $alpha: 0.3);
  text-shadow: 0  0 5px rgba($color: #727070, $alpha: 0.3) ;
  
  
  text-align: center;
  font-size: 60px;
  font-weight: 800;
  z-index: 20;
  }
