
.footerBlock {
    min-height: 60vh;
    display: flex;
    align-items: center;
    background-color: lightgrey;
    color: darkolivegreen;
    .footerContainer {
        height: 100%;
        width: 80%;
        margin: 0 auto;
        padding: 3vh 0;
        display: flex;
        align-items: center;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: space-between;
        border: 1px solid;
        border-radius: 3px;
        .footerNav {

            width: 80%;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-around;

            border-radius: 3px;
            background-color: darkseagreen;
            .link {
                width: 77px;
                height: 77px;
                margin: 1px 0;
                background-color: grey;
            }

        }
        .footerEnd {

            font-size: 20px;
        }

    }
}







