
.removeWorkBlock {
  min-height: 40vh;
  display: flex;
  align-items: center;
  background-color: lavender;

  .removeWorkContainer {
    height: 100%;
    width: 80%;
    margin: 0 auto;
    padding: 3vh 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid;
    border-radius: 3px;

    .title {
      text-align: center;
      font-size: 33px;
    }

    .linkOffRemoveWork {
      font-family: Inconsolata;
      font-weight: bolder;
      font-size: 35px;
      padding: 10px;
      border: 2px solid lightskyblue;
      border-radius: 5px;
      background-color: powderblue;
      text-decoration: none;
      color: white;
    }


  }

}



